import React from 'react';
import LeftBar from './LeftBar';
import { Breadcrumb } from 'antd';
import {routes,reactRoutes} from './route';
import { withRouter } from "react-router";
import './index.less'
const Help = ({location})=>{

    // 根据当前路径获取当前面包屑的名称
    let localName = '';
    routes.forEach(({name,path})=>{
        if(path === location.pathname){
            localName = name;
        }
    })

    return <div className='component-help clearfix'>
        <LeftBar routes={routes}/>
        <div className='card pull-left'>
            <Breadcrumb separator=">>">
                <Breadcrumb.Item>帮助中心</Breadcrumb.Item>
                <Breadcrumb.Item>{localName}</Breadcrumb.Item>
            </Breadcrumb>
            {reactRoutes}
        </div>
    </div>
}

export default withRouter(Help);