import axios from '../../utils/axios';
import qs from 'qs';
import {Modal} from 'antd';

const ToastInfo = (info)=>{
    Modal.error({
        title: '提示',
        content: info,
        okText:'确定',
        centered:true,
      });
}

const getCertNo = (phoneNo) =>{
    return axios.get('index.php', {
        params:{
            m: 'Home',
            c:'Api',
            a:'send_validate_code',
            scene:1,
            type:'mobile',
            mobile:phoneNo,
        }
    }).then(({data={}})=>{
        if(data.status === 1){
            return true;
        }else{
            ToastInfo(data.msg || '今天获取验证码超过10条，明天才可以获取');
            return false;
        }
    }).catch((e)=>{
        console.error("index.php",e);
        ToastInfo('服务异常');
        return false;
    })
}

const login = (userName,verify_code)=>{
    return axios.post('index.php/Admin/Api/do_login', qs.stringify({
        username:userName,
        verify_code,
      }),{
            headers:{
                'Content-Type':'application/x-www-form-urlencoded; charset=UTF-8',
            },
      }).then(({data={}})=>{
        if(data.status===-1){
            ToastInfo(data.msg);
            return false;
        }

        var obj = {
            username : data.user.mobile,
            user_id:data.user.user_id,
        };
        localStorage.setItem('ererPcuserObj',JSON.stringify(obj));
        window.location.href = '../xiazai/shopmsg.html'
        return true;
      }).catch(e=>{
         console.error("index.php/Admin/Api/do_login",e);
         ToastInfo('服务异常');
        return false;
      });
} 

export {
    getCertNo,
    login,
}
