import React from 'react';
import { Link } from "react-router-dom";
import { withRouter } from "react-router";
import './index.less'

const LeftBar = ({routes=[], className, location})=>{
    return <div className={`component-help-leftbar ${className || ''}`}>
        <h5>帮助中心</h5>
        {
            routes.map(({name,path},index)=>{
                return path?<div key={index} className={`nav-link ${location.pathname === path?"active":''}`}><Link to={path}>{name}</Link></div>:false;
            }).filter(ele=>!!ele)
        }
    </div>
};

export default withRouter(LeftBar);
