import React from 'react';
import { Modal, Button, Input, Icon, Form  } from 'antd';
import {getCertNo,login} from './action';
import './index.css';

const ToastInfo = (info)=>{
    Modal.error({
        title: '提示',
        content: info,
        okText:'确定',
        centered:true,
      });
}

// 60s后重新获取验证码
const getCertAtfer = 60;

class LoginModal extends React.Component{
    state={
        visible:false,
        // 关于验证码
        getCerting:false,
        certNoTime:0,

        // 关于登录
        logining:false,

    }
    componentDidMount(){
        let nodeList = document.body.querySelectorAll('[href="#login-modal"]'),len = nodeList.length;
        for(let i=0;i<len;i++){
            nodeList[i].addEventListener('click',this.showModal,false);
        }
    }
    componentWillUnmount(){
        let nodeList = document.body.querySelectorAll('click','[href="#login-modal"]'),len = nodeList.length;
        for(let i=0;i<len;i++){
            nodeList[i].removeEventListener(this.showModal);
        }
    }
    showModal = () => {
        this.setState({
            visible: true,
        });
    }
    handleCancel = e => {
        this.setState({
          visible: false,
        });
      };
    countTimer = ()=>{
        const {certNoTime} = this.state;
        this.setState({certNoTime:certNoTime-1},()=>{
            if(certNoTime>1){
                setTimeout(this.countTimer,1000);
            }
        });
    }
    getCert = (e)=>{
        this.props.form.validateFields((err, values)=>{
            if(this.validatePhoneNo(values.phoneNo)){
                this.setState({getCerting:true});
                getCertNo(values.phoneNo).then((res)=>{
                    if(res){
                        this.setState({getCerting:false,certNoTime:getCertAtfer},()=>{
                            setTimeout(this.countTimer,1000)
                        });
                    }
                    this.setState({getCerting:false});
                }).catch(e=>{
                    this.setState({getCerting:false});
                });
            }
        })
    }
    validatePhoneNo = (phoneNo)=>{
        if(!phoneNo){
            ToastInfo('请输入手机号');
        } else if(!/(^1[3-9]\d{9}$)|(^09\d{8}$)/.test(phoneNo)){
            ToastInfo('请输入正确格式的手机号码');
        }else{
            return true;
        }
    }
    validateCertNo = (CertNo)=>{
        if(!CertNo){
            ToastInfo('请输入验证码');
        }else{
            return true;
        }
    }
    handleSubmit = (e)=>{
        e && e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(this.validatePhoneNo(values.phoneNo) && this.validateCertNo(values.certNo)){
                this.setState({logining:true});
                login(values.phoneNo,values.certNo).then((res)=>{
                    this.setState({logining:false});
                }).catch(e=>{
                    this.setState({logining:false});
                })
            }
            
        });
    }
    render(){
        const { getFieldDecorator } = this.props.form,
            {getCerting,certNoTime,logining} = this.state;
        return <React.Fragment>
            <Modal
            title="欢迎入驻"
            visible={this.state.visible}
            centered={true}
            footer={null}
            closable={false}
            className='component-login-modal'
            style={{width: '33.2vw'}}
            maskClosable={true}
            onCancel={this.handleCancel}
            >
            <Form onSubmit={this.handleSubmit} className="component-login-form">
                <Form.Item>
                {getFieldDecorator('phoneNo')(
                    <Input
                        placeholder="请输入手机号"
                        prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    />,
                )}
                </Form.Item>
                <Form.Item>
                {getFieldDecorator('certNo')(
                    <Input
                    placeholder="请输入验证码"
                    prefix={<Icon type="safety-certificate" style={{ color: 'rgba(0,0,0,.25)'}}/>}
                    addonAfter={
                        getCerting?<span className='info'>获取中...</span>:
                        (certNoTime === 0?
                        <span className='btn-addon info' onClick={this.getCert}>获取验证码</span>:
                        <span className='info'>{`${certNoTime}秒后获取`}</span>
                        )
                    }
                    />,
                )}
                </Form.Item>
                
                <Button type="primary" htmlType="submit" className="login-form-button" block={true} loading={logining}>
                    登录
                </Button>
            </Form>
            </Modal>
        </React.Fragment>
    }
}

export default Form.create({ name: 'component-login-form' })(LoginModal);