import React from 'react';
import './index.less';

const Item = ({info,images,imageWidth,index,children})=>{
    return <div className='component-help-item'>
        <p>{index+1}、{info}</p>
        <div className='images'>
            {
                images.map((src,index)=><img key={index} width={imageWidth} src={src} alt=''/>)
            }
            {children}
        </div>
    </div>
}

const List = ({data=[],imageWidth='100%',children})=>{
    return <React.Fragment>
    {
        data.map((item,index)=>item && <Item key={index}  {...item} imageWidth={imageWidth} index={index}/>)
    }
    {children}
    </React.Fragment>;
}

export default List;