//如何使用积分
const Points = ()=>{
    let list = [{
        images:[`${path}/help_points_1.png`,`${path}/help_points_2.png`,`${path}/help_points_3.png`],
        info:"用户可使用积分抵扣或购买商品，当使用积分购买的商品需要退换货时，积分不退还至账户且换货需要补齐积分所抵扣的差价。"
    },{
        images:[`${path}/help_points_4.png`],
        info:"首次注册的新用户可获得50积分。"
    },{
        images:[`${path}/help_points_5.png`],
        info:"购买商品时，可获得购买商品所赠积分，所赠积分需要在确认收货后七天存入账户。"
    }];
    return <List data={list} imageWidth='30%'/>
}


export const generateRoutes = (routes)=>{
    return routes.map(({path,component,from,to,isRedirect},index)=>{
        if(isRedirect){
            return <Redirect key={index} to={to}/>
        }else{
            return <Route key={index} path={path} component={component} exact/>
        }
    })
}

export const routes = [{
        name:'如何入驻平台',
        path:'/join',
        component:Join,
    },{
        name:'如何指派商品',
        path:'/assign',
        component:Assign,
    },{
        name:'如何使用积分',
        path:'/points',
        component:Points,
    },{
        isRedirect:true,
        to:"/join",
    }];

export const reactRoutes = <Switch>{generateRoutes(routes)}</Switch>;